<template>
  <div class="UserInformation">
        <!-- 这是间隔布局 -->
      <div class="left">
          <!-- <div v-for="(item,index) in Dleft"  :key="index" @click="Fnleft(index)" :class="[{active:index==DleftInd}]">
              {{item.lname}}
          </div> -->
          <div @click="Fnleft(0)">
              我的信息
          </div>
          <!-- <div @click="Fnleft(1)">
              修改信息
          </div> -->
          <div @click="Fnleft(2)">
              更改密码/手机号
          </div>
          <div @click="Fnleft(3)" v-if="!userInfo.is_staff">
              创建公司
          </div>
          <div @click="Fnleft(4)" v-if="!userInfo.is_staff">
              加入公司
          </div>
          <div @click="Fnleft(5)" v-if="userInfo.is_staff">
              我的公司
          </div>
          <div @click="Fnleft(6)" v-if="userInfo.is_staff">
              去首页
          </div>
      </div>
      <div class="right">
        <router-view/>
        <!-- 绑定公司 -->
      </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
  export default {
    data() {
      return {
        form: {//这是提交的表单信息
          name: '',
          region: '',
          imageUrl: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        Dleft:[//左侧的信息条目
            {
                icon:'',
                lname:'我的信息'
            },
            {
                icon:'',
                lname:'修改信息'
            },
            {
                icon:'',
                lname:'更改密码'
            },
            {
                icon:'',
                lname:'创建公司'
            },
            {
                icon:'',
                lname:'加入公司'
            },
            {
                icon:'',
                lname:'我的公司'
            },
        ],
        DleftInd:0 || sessionStorage.getItem('i'),//左侧信息下标
        dialogFormVisible: false,//这是修改弹出啊内容
      };
    },
    methods: {
      Fnleft(i){//左侧导航栏点击事件
          sessionStorage.setItem("i",i);
          this.DleftInd=sessionStorage.getItem('i')
          console.log(i,'我被触发了')
          if(i==2){//更改密码
            this.$router.push('/UserInformation/UIChangepassword')
          }else if(i==3){//创建公司
            this.$router.push('/UserInformation/createcompany')
          }else if(i==4){//加入公司
            this.$router.push('/UserInformation/joincompany')
          }else if(i==5){//我的公司
            this.$router.push('/UserInformation/myCompany')
          }else if(i==0){//编辑个人信息
            this.$router.push('/UserInformation/UItwo')
          }else if(i==6){//去首页
            this.$router.push('/homeIndex')
          }
          // switch (i) {
          //   case 0:
              
          //     break;
          
          //   default:
          //     break;
          // }
      },

    },
    computed:{
      ...mapState(['userInfo'])
    }
  }
</script>

<style lang="stylus" scoped>
.UserInformation
    margin 20px 80px 0 80px
    display flex
    .left
        width 260px
        margin-right 20px
        border-right 1px solid #f00
        div
            line-height 50px
            color #333
            padding-left 15px
            border-radius 9px
        .active
            background #f0f0f0
    .right
        width 800px
        margin-top 15px
        .imgShow 
            width 65px
            height 65px
            border-radius 50%
            box-shadow: 3px 3px 3px #888888;
            margin-right 20px
            margin-left 15px
        .list
            display flex
            line-height 50px
            border-bottom 1px dashed #ccc
            div:first-of-type
                width 120px
                margin-right 15px
                color #999
            div:last-of-type
                flex 1
                color #333
  >>>.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  >>>.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  >>>.avatar-uploader-icon {
    font-size: 20px;
    color: #8c939d;
    width: 90px;
    height: 90px;
    line-height: 90px;
    text-align: center;
  }
  >>>.avatar {
    width: 90px;
    height: 90px;
    display: block;
  }
</style>


